<template>
  <v-container fluid>
<!--    <v-select v-model="note" :items="notes" @change="getScaleNotes"></v-select>-->
    <v-row>
      <v-col  cols="3">
        <div  class="v-input v-input--is-label-active v-input--is-dirty theme--light v-text-field v-text-field--is-booted v-text-field--enclosed v-text-field--outlined v-select v-select--chips v-autocomplete">
<div class="v-input__control">
          <div class="v-input__slot">
        <fieldset aria-hidden="true">

            <legend style="width: 35.25px;"><span>&#8203;</span></legend>
        </fieldset>
          <div class="v-select__slot">
            <label for="noteInput" class="v-label v-label--active theme--light" style="left: 0px; right: auto; position: absolute;">
              Note</label>
        <v-item-group class="mt-2" id="noteInput" v-model="note" @change="getScaleNotes">
                <v-item v-for="n in 12" :key="notes[n-1]" class="mx-1"
                        v-bind:class="{
                          'mt-8': !notes[n-1].endsWith('#'),
                          'mt-n8': notes[n-1].endsWith('#'),
                          'mx-n5': notes[n-1].endsWith('#')}"
                    :value="notes[n-1]"
                    v-slot="{ active, toggle }"
                >
                  <v-chip
                      active-class="deep-purple--text text--accent-4"
                      :input-value="active"
                      @click="toggle"
                  >
                    {{ notes[n-1] }}
                  </v-chip>
                </v-item>

        </v-item-group>
          </div>
        </div>
        </div>
        </div>
      </v-col>
      <v-col  cols="2">
        <v-combobox v-model="scale"
                    :items="scales"
                    label="Scale" chips
                    outlined
                    attach
                    @change="getScaleNotes"></v-combobox>
      </v-col>
    </v-row>

  <div class="fretboard">
    <div class="string" v-for="(string, stringIndex) in strings" v-bind:key="stringIndex">
      <div class="fret" v-for="(fretNote, fretIndex) in string" v-bind:key="fretIndex"
      :class="{'single-fretmark':stringIndex==0 && [3,5,7,9].includes(fretIndex),
              'double-fretmark':stringIndex==0 && [12].includes(fretIndex)}">
        <div class="note" :class="{'note-on':calcNote(fretNote)!=' '}"
        @click="playTone(fretNote)"
        >{{calcNote(fretNote)}}</div>
      </div>
    </div>
  </div>

  </v-container>
</template>

<script>
const { Note, Interval, Scale } = require("@tonaljs/tonal");
import * as Tone from 'tone'
//create a synth and connect it to the main output (your speakers)
const synth = new Tone.Synth().toDestination();
export default {
  name: "Fretboard",
  data: function(){
    return {
      stringTunings:[
        {note:'E',octave:5},
        {note:'B',octave:4},
        {note:'G',octave:4},
        {note:'D',octave:4},
        {note:'A',octave:3},
        {note:'E',octave:3}
      ],
      startFret:0,
      endFret:18,
      strings:[],
      scaleNotes:[],
      scale:'major',
      note:'C'
    }
  },
  computed:{
    scales(){
      let scales= Scale.names();
      scales.sort();
      return scales;
    },
    notes(){
      return ['C','C#','D','D#','E','F','F#','G','G#','A','A#','B'];
      // Note.names();
    },
  },
  methods:{
    playTone(note){
      synth.triggerAttackRelease(note.name, "8n");
    },
    getScaleNotes(){
      if (this.note && this.scale){
        this.scaleNotes=[];
        Scale.get(this.note + ' ' + this.scale).notes.forEach(scaleNote => this.scaleNotes.push(Note.get(scaleNote)));
        console.log(this.scaleNotes)
      }
    },
    calcNote(fretNote){
      let  note =' ';
      if (this.scaleNotes)
      // return fretNote.pc;
        this.scaleNotes.forEach((scaleNote, index)=>{
          if(scaleNote.chroma==fretNote.chroma){
            // console.log(scaleNote.pc)
            note = (index+1)+scaleNote.pc;
          }
        })
       return note;
    },
    getFretNotes(){
      this.stringTunings.forEach((tuning) => {
            let frets = [];
            for(let i=0;i<(this.endFret-this.startFret);i++){
              let semitones = Interval.fromSemitones(i);
              let transposed = Note.get(Note.transpose(tuning.note+tuning.octave, semitones))
              frets.push(transposed)
            }
            this.strings.push(frets);
          }
      )
    }
  },
  created: function(){
    this.getFretNotes();
    this.getScaleNotes();
  }
}
</script>

